import React, { useState } from "react";
import { Link } from "react-router-dom";
// import { useDispatch, useSelector } from "react-redux";
// import { logger, modalNotification } from "../../../../utils";
import { Form, Formik } from "formik";
import { useDispatch, useSelector } from "react-redux";
import {
  AcountSidebar,
  //   Breadcrumb,
  Input as TextInput,
  MetaTags,
  ModalComponent,
  PageHeader,
  CommonButton,
  AntTooltip as Tooltip,
  UploadInput,
  //   PageHeader,
  //   ProfileForm,
} from "../../../../components";
import { Media } from "../../../../apiEndPoints";
import {
  selectUserData,
  updateUserDataAction,
} from "../../../../redux/AuthSlice/index.slice";
import { subAdminAuthServices } from "../../../../services";
import { logger, modalNotification } from "../../../../utils";
import validation from "./validation";

function AdminProfile() {
  const dispatch = useDispatch();
  const [loading, setLoading] = useState(false);
  const [showEdit, setShowEdit] = useState(false);
  const userData = useSelector(selectUserData);

  const initialValues = {
    firstName: userData?.firstName || "",
    lastName: userData?.lastName || "",
    // profilePicture: userData?.selfieImage || "",
    phoneNumber: userData?.phoneNumber || "",
    // phoneNumberCountryCode: "+91",
  };
  const closeEditProfileModal = () => {
    setShowEdit(false);
  };
  const [asideView, setAsideView] = useState(false);
  const asideToggle = () => {
    setAsideView(!asideView);
  };
  if (asideView) {
    document.querySelector("body").classList.add("toggle-shown");
  } else {
    document.querySelector("body").classList.remove("toggle-shown");
  }

  const onSubmit = async (value) => {
    setLoading(true);
    try {
      let bodyData = { ...value };
      bodyData.email = userData?.email;
      bodyData.countryCode = userData?.countryCode || "+91";
      // bodyData.phoneNumber = `${bodyData.phoneNumber}`;
      const response = await subAdminAuthServices.editProfileService(bodyData);
      const { success, message } = response;
      if (success) {
        modalNotification({
          type: "success",
          message,
        });
        setLoading(false);
        dispatch(updateUserDataAction(response?.data));
      }
    } catch (error) {
      logger(error);
    }
    setLoading(false);
  };

  return (
    <>
      <MetaTags title="" />

      <div className="nk-block">
        <div className="card">
          <div className="card-aside-wrap">
            <div className="card-inner card-inner-lg">
              <div className="nk-block-head nk-block-head-lg">
                <div className="nk-block-between">
                  <PageHeader heading="Personal Information">
                    <p>
                      Basic info, like your name and address, that you use on
                      Nio Platform.
                    </p>
                  </PageHeader>
                  <div className="nk-block-head-content align-self-start d-lg-none">
                    <Link
                      to="#"
                      onClick={() => asideToggle()}
                      className={`toggle btn btn-icon btn-trigger mt-n1 ${
                        asideView ? "active" : ""
                      }`}
                    >
                      <em className="icon ni ni-menu-alt-r" />
                    </Link>
                  </div>
                </div>
              </div>
              <Formik
                initialValues={{ ...initialValues }}
                validationSchema={validation()}
                onSubmit={onSubmit}
                //   innerRef={formRef}
              >
                {(props) => {
                  return (
                    <Form>
                      <div className="form-group">
                        <div className="form-control-wrap language-flag">
                          <div className="upload_photo mb-2 mb-md-3 mx-auto text-center">
                            <div className="img-box">
                              <UploadInput
                                name="profilePicture"
                                apiEndPoints={Media.profileImage}
                                type="file"
                                // disabled={!isEdit}
                                defaultImageUrl={userData?.profilePicture || ""}
                                setFieldValue={props.handleChange}
                                profilePicture
                              >
                                <Tooltip
                                  placement="right"
                                  color="#5D4BFF"
                                  promptText="Upload either a JPEG, JPG or PNG file not exceeding 5MBs in size"
                                >
                                  <label className="mb-0 ripple-effect filelabel">
                                    <em className="icon ni ni-pen2" />
                                  </label>
                                </Tooltip>
                              </UploadInput>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div className="form-group mb-md-3 mb-2">
                        <div className="row">
                          <div className="col-6">
                            <label className="form-label">First Name</label>
                            <div className="form-control-wrap">
                              <TextInput
                                className="form-control shadow-none"
                                name="firstName"
                                // disabled={false}
                                variant="standard"
                                type="text"
                                placeholder="Enter first Name"
                                setFieldValue={props.handleChange}
                              />
                            </div>
                          </div>
                          <div className="col-6">
                            <label className="form-label">Last Name</label>
                            <div className="form-control-wrap">
                              <TextInput
                                className="form-control shadow-none"
                                name="lastName"
                                // disabled={false}
                                variant="standard"
                                type="text"
                                placeholder="Enter last Name"
                                setFieldValue={props.handleChange}
                              />
                            </div>
                          </div>
                        </div>
                      </div>
                      <div className="form-group">
                        <div className="form-label-group">
                          <label className="form-label">Mobile Number</label>
                        </div>
                        <div className="row g-2">
                          <div className="countryCode col-4 col-sm-2">
                            <div className="form-control-wrap">
                              <TextInput
                                type="text"
                                className="form-control"
                                placeholder="Country code"
                                value="+91"
                                name="countryCode"
                                disabled={false}
                                variant="standard"
                                readOnly
                              />
                            </div>
                          </div>
                          <div className="col-8 col-sm-10">
                            <div className="form-control-wrap">
                              <TextInput
                                className="form-control"
                                name="phoneNumber"
                                // disabled={false}
                                variant="standard"
                                type="number"
                                placeholder="Enter Mobile Number"
                                setFieldValue={props.handleChange}
                                //   onKeyDown={onKeyChange}
                              />
                            </div>
                          </div>
                        </div>
                      </div>
                      <div className="form-group ">
                        <div className="form-label-group">
                          <label className="form-label">Email Address : </label>
                        </div>
                        <div>backend@mailinator.com</div>
                      </div>
                      <div className="formAction text-end">
                        <CommonButton
                          extraClassName="btn btn-primary flex-shrink-0 w190 btn-gradiant saveButton"
                          htmlType="submit"
                          type="submit"
                          loading={loading}
                        >
                          Update
                        </CommonButton>
                      </div>
                    </Form>
                  );
                }}
              </Formik>
            </div>
            <AcountSidebar asideView={asideView} asideToggle={asideToggle} />
          </div>
        </div>
      </div>

      <ModalComponent
        closeButton={false}
        extraTitleClassName="justify-content-center"
        onHandleCancel={() => {
          setShowEdit(false);
        }}
        backdrop
        show={showEdit}
        title="Update Profile"
      >
        <form className="w-100">
          <div className="form-group">
            <label className="form-label" htmlFor="full-name">
              Full Name
            </label>
            <TextInput
              id="full-name"
              className="form-control form-control-lg"
              name="fullName"
              disabled={false}
              variant="standard"
              type="text"
              placeholder="Enter full name"
              value="Abu Bin Ishtiyak"
              icon=""
            />
          </div>
          <div className="form-group">
            <label className="form-label" htmlFor="display-name">
              Display Name
            </label>
            <TextInput
              id="display-name"
              className="form-control form-control-lg"
              name="displayName"
              disabled={false}
              variant="standard"
              type="text"
              placeholder="Enter display name"
              value="Ishtiyak"
              icon=""
            />
          </div>
          <div className="form-group">
            <label className="form-label" htmlFor="email">
              Email
            </label>
            <TextInput
              id="email"
              className="form-control form-control-lg"
              name="email"
              disabled
              variant="standard"
              type="email"
              placeholder="Enter email"
              value="info@dakkia.com"
              icon=""
            />
          </div>
          <div className="mt-4 align-center justify-content-center flex-wrap flex-sm-nowrap gx-1 gy-2">
            <div>
              <Link
                href="#"
                onClick={() => closeEditProfileModal()}
                className="btn btn-primary"
              >
                Update Profile
              </Link>
            </div>
            <div>
              <Link
                href="#"
                onClick={() => closeEditProfileModal()}
                className="btn btn-secondary"
              >
                Cancel
              </Link>
            </div>
          </div>
        </form>
      </ModalComponent>
    </>
  );
}

export default AdminProfile;
